// src/api/openai.js
export async function getBotReply(message) {
  try {
    const res = await fetch("http://localhost:5500/api/chat", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ message }),
    });
    const data = await res.json();
    return data.reply;
  } catch (err) {
    console.error(err);
    return "Sorry, I couldn't think of a reply.";
  }
}
