import React, { useEffect, useRef, useState } from "react";
import { useGoals } from "../../context/GoalContext";
import { getBotReply } from "../../api/openai";
import bot1 from "../../assets/bot1.png";
import "./VoiceParrotBot.css";
import { db } from "../../Firebaseconfig";
import { collection, addDoc, Timestamp } from "firebase/firestore";

export default function VoiceParrotBot() {
  const [listening, setListening] = useState(false);
  const [spokenText, setSpokenText] = useState("");
  const [botReply, setBotReply] = useState("");
  const [status, setStatus] = useState(""); // show firebase save status
  const recognitionRef = useRef(null);
  const { addGoal } = useGoals();

  useEffect(() => {
    const SpeechRecognition =
      window.SpeechRecognition || window.webkitSpeechRecognition;

    if (!SpeechRecognition) {
      alert("Your browser does not support Speech Recognition.");
      return;
    }

    const recognition = new SpeechRecognition();
    recognition.lang = "en-US";
    recognition.continuous = true;
    recognition.interimResults = true;

    recognition.onresult = (event) => {
      let text = "";
      for (let i = event.resultIndex; i < event.results.length; i++) {
        text += event.results[i][0].transcript;
      }
      setSpokenText(text);

      if (event.results[event.results.length - 1].isFinal) {
        handleUserSpeech(text);
      }
    };

    recognition.onend = () => setListening(false);
    recognitionRef.current = recognition;
  }, []);

  const handleUserSpeech = async (text) => {
    const lower = text.toLowerCase();

    // Goal tracking
    if (lower.includes("i want to") || lower.includes("remind me to")) {
      addGoal(text);
    }

    // Get AI reply
    let reply = "";
    try {
      reply = await getBotReply(text);
    } catch (err) {
      reply = "Sorry, I couldn't process that.";
      console.error("OpenAI reply error:", err);
    }
    setBotReply(reply);
    speakText(reply);

    // Store conversation in Firebase
    try {
      await addDoc(collection(db, "conversations"), {
        user: text,
        bot: reply,
        createdAt: Timestamp.now(),
      });
      setStatus("âœ… Conversation saved to Firebase!");
      console.log("Conversation saved to Firebase:", { user: text, bot: reply });
    } catch (error) {
      setStatus("âŒ Failed to save conversation.");
      console.error("Firebase save error:", error);
    }

    setTimeout(() => setStatus(""), 5000); // hide status after 5s
  };

  const startListening = () => {
    if (recognitionRef.current) {
      setSpokenText("");
      setBotReply("");
      recognitionRef.current.start();
      setListening(true);
    }
  };

  const stopListening = () => {
    if (recognitionRef.current) {
      recognitionRef.current.stop();
      setListening(false);
    }
  };

  const speakText = (text) => {
    if (!window.speechSynthesis) return;
    const utterance = new SpeechSynthesisUtterance(text);
    utterance.lang = "en-US";
    utterance.pitch = 1;
    utterance.rate = 1;
    window.speechSynthesis.speak(utterance);
  };

  const stopSpeaking = () => {
    if (window.speechSynthesis) {
      window.speechSynthesis.cancel();
    }
  };

  return (
    <div className="voice-bot-container">
      <div className="voice-bot-right">
        <img src={bot1} alt="Voice AI" />
      </div>
      <div className="voice-bot-left">
        <h1>ðŸŽ™ EmiBot Voice Companion</h1>
        <p>Speak to EmiBot and it will respond intelligently, track your goals, and repeat your voice!</p>

        <div className="voice-buttons">
          <button
            className={listening ? "stop-listen-btn" : "start-listen-btn"}
            onClick={listening ? stopListening : startListening}
          >
            {listening ? "Stop Listening" : "Start Listening"}
          </button>
          <button className="stop-speak-btn" onClick={stopSpeaking}>
            Stop Speaking
          </button>
        </div>

        {status && <p className="firebase-status">{status}</p>}

        <div className="voice-textbox">
          <strong>You:</strong>
          <p>{spokenText || "Say something..."}</p>
        </div>

        <div className="voice-textbox bot-text">
          <strong>Bot:</strong>
          <p>{botReply || "I'll reply here..."}</p>
        </div>
      </div>
    </div>
  );
}
